import { useParams, useHistory } from "react-router-dom";
import { useState } from "react";
import useFetch from "../hooks/useFetch";
import firebase from "../../firebase";
import "./blogDetails.css";

const BlogDetails = () => {
   const { id } = useParams();
   const history = useHistory();
   const [blog, setBlog] = useState(null);
   const { isLoading, error } = useFetch("blog1");

   const db = firebase.firestore().collection("blog1");
   db.doc(id)
      .get()
      .then((doc) => {
         setBlog(doc.data());
      });

   const deleteBlog = () => {
      db.doc(id)
         .delete()
         .then(() => {
            history.push("/dojoBlog");
         });
   };

   return (
      <div className="blog-details">
         {isLoading && (
            <div className="progress">
               <div className="indeterminate pink accent-3-only"></div>
            </div>
         )}
         {error && (
            <div style={{ fontSize: "20px", fontWeight: "700" }}>{error}</div>
         )}
         {blog && (
            <article>
               <h2>
                  <span>{blog.title}</span>
                  <i
                     className="material-icons right deleteIcon"
                     style={{
                        cursor: "pointer",
                        fontSize: "40px",
                     }}
                     onClick={deleteBlog}
                  >
                     delete
                  </i>
               </h2>
               <h6>Written by {blog.author}</h6>
               <div>
                  <p>
                     <b>{blog.body}</b>
                  </p>
               </div>
            </article>
         )}
      </div>
   );
};

export default BlogDetails;
