import { useHistory } from "react-router-dom";
import firebase from "../../firebase";
import { useState } from "react";
import logo from "./github.png";
import "./CSS/contact.css";
import "./CSS/loader.css";
import "./CSS/modal.css";

const Contact = () => {
   const history = useHistory();
   const [email, setEmail] = useState("");
   const [message, setMessage] = useState("");
   const [isSending, setIsSending] = useState(false);
   const db = firebase.firestore().collection("messages");

   const handleSubmit = (e) => {
      e.preventDefault();
      setIsSending(true);

      db.add({
         email: email,
         message: message,
      }).then(() => {
         openModal();
         setIsSending(false);
      });
   };

   const openModal = () => {
      var elem = document.querySelector(".modal");
      var instance = window.M.Modal.init(elem);
      instance.open();
   };

   const closeModal = () => {
      var elem = document.querySelector(".modal");
      var instance = window.M.Modal.init(elem, {});
      instance.close();
      setTimeout(() => {
         history.push("/dojoBlog");
      }, 1000);
   };

   return (
      <div className="contact">
         <section className="infomodal">
            <div id="modal3" className="modal">
               <div className="modal-content">
                  <h3 id="info-modal-heading" style={{ color: "#f1356d" }}>
                     Thanks
                  </h3>
                  <p className="info-modal-heading">
                     I hope to read your message soon
                  </p>
               </div>
               <div className="modal-footer">
                  <i
                     className="material-icons closeIcon"
                     style={{ fontSize: "30px" }}
                     onClick={() => {
                        closeModal();
                     }}
                  >
                     close
                  </i>
               </div>
            </div>
         </section>

         <section id="form">
            <h2>Contact Me</h2>
            <form onSubmit={handleSubmit}>
               <input
                  type="email"
                  name="email"
                  required
                  placeholder="Email Address"
                  onChange={(e) => {
                     setEmail(e.target.value);
                  }}
               />
               <input
                  type="text"
                  name="message"
                  required
                  placeholder="Your Message"
                  onChange={(e) => {
                     setMessage(e.target.value);
                  }}
               />
               {!isSending && (
                  <button
                     type="submit"
                     className="right waves-effect waves-light"
                  >
                     Send
                  </button>
               )}
               {isSending && (
                  <span className="loader right">
                     <span className="loader-inner"></span>
                  </span>
               )}
            </form>
         </section>

         <div id="footer" className="hide-on-large-only">
            <a href="https://github.com/mohdAffancodes">
               <img src={logo} alt="githubImg" />
            </a>
            <div className="copyRight">©mohdAffancodes</div>
         </div>
      </div>
   );
};

export default Contact;
